import { Component, OnInit } from '@angular/core';
import { InicioService } from '../../../providers/Almacenamiento/inicio.service';
import { InventariosService } from '../../../providers/Api/Inventarios.service';
import { ToastServices } from '../../../providers/Utilidades/ToastService.service';
import { ActivatedRoute, Router } from '@angular/router';
import { AlertController } from '@ionic/angular';

@Component({
  selector: 'app-ivt-configuracion',
  templateUrl: './ivt-configuracion.page.html',
  styleUrls: ['./ivt-configuracion.page.scss'],
})
export class IvtConfiguracionPage implements OnInit {

  nombre = '';
  cambio = false;

  constructor(
    private almacenamiento: InicioService,
    private ivtApi: InventariosService,
    private toast: ToastServices,
    private activeRoute: ActivatedRoute,
    private alertCtrl: AlertController,
    private router: Router
  ) { }

  ngOnInit() { }

  ionViewWillEnter() {
    this.cargarCategorias();
  }

  refrescando( event ) {
    this.cargarCategorias().then(() => {
      event.target.complete()
    }).catch(() => event.target.complete())
  }

  async cargarCategorias() {
      const info = await this.almacenamiento.obtenerToken();

      if ( info ) {
        this.ivtApi.leerInventario(info.token, this.activeRoute.snapshot.paramMap.get('id')).subscribe((data: any) => {
          if (data.exe) {
              this.nombre = data.response.name;
          } else {
            this.toast.toastError('No se pudo cargar los datos, intentelo más tarde');
          }
        }, (err) => {
            this.toast.toastError('No se pudo cargar los datos, intentelo más tarde');
        })
      }
  }

  toggle( event ) {
    console.log(event)
    this.cambio = event.detail.checked;
  }

  async guardar() {
    if (this.cambio) {
        const alert = await this.alertCtrl.create({
          header: 'Cerrar periodo',
          message: 'Al cerrar el periodo de inventario comezará un nuevo periodo a partir de ahora. Esta acción no se puede revsersar',
          buttons: [
            {
              text: 'cancelar',
              role: 'cancel'
            },
            {
              text: 'Aceptar',
              handler: () => {
                this.actualizar(true);
              }
            }
          ]
        })

        await alert.present();
    } else {
      this.actualizar(false);
    }
  }

  async actualizar(bool) {
      const info = await this.almacenamiento.obtenerToken();

      if ( info ) {
        this.ivtApi.actualizarInventario(info.token, {ivt: this.activeRoute.snapshot.paramMap.get('id'), nombre: this.nombre, exe: bool}).subscribe((data: any) => {
          if ( data.exe ) {
            this.toast.toastExitoso('Información actualizada!');
            this.router.navigate(['/central/menu/ivt-miinventario', this.activeRoute.snapshot.paramMap.get('id')]);
          } else {
            this.toast.toastError('No se pudo actualizar el inventario, intentelo más tarde');
          }
        }, (err) => {
          this.toast.toastError('No se pudo actualizar el inventario, intentelo más tarde');
        })
      }
  }


}
